.home {
  h2, h1 {
    font-size: 80px;
    font-weight: bold;
    line-height: 0.94;
    letter-spacing: -4px;
    text-align: center;
    color: #131c2e;
    max-width: 880px;
    position: relative;
    left: 50%;
    transform: translate(-50%);
    width: 100%;
    @include r(767) {
      font-size: 34px;
      letter-spacing: normal; } }
  h1 {
    line-height: 1.06; }
  .intro {
    background-image: url('../img/home/home-header.png');
    background-position: 100% 0;
    background-size: auto 90%;
    min-height: 820px;
    @include r(767) {
      min-height: 100%;
      padding: 120px 0 50px; }
    @include r(550) {
      background-size: auto 70%; }
    @include r(450) {
      background-size: 85% auto; }
    h1 {
      margin-bottom: 45px; }
    p {
      color: #616161;
      max-width: 640px;
      margin-bottom: 70px;
      @include r(767) {
        margin-bottom: 40px; } }
    a {
      background: #131c2e;
      @include tr;
      @include center;
      width: 265px;
      height: 60px;
      border-radius: 30px;
      font-size: 18px;
      border: none;
      display: flex !important;
      font-weight: bold;
      line-height: 1.39;
      color: #ffffff;
      &:hover {
        background: rgba(#131c2e, 0.8);
        i {
          transform: translate(8px); } }
      i {
        font-size: 14px;
        color: #006dff;
        margin-left: 13px;
        @include tr; } } }
  &__features {
    &__text {
      padding: 140px 0;
      max-width: 1040px;
      width: 100%;
      @include relative-x;
      @include r(767) {
        padding: 50px 0 70px; }
      p {
        font-size: 25px;
        line-height: 1.4;
        text-align: center;
        color: #131c2e;
        margin-bottom: 35px;
        @include r(767) {
          font-size: 18px; }
        b {
          font-weight: bold;
          color: #ff0057; } }
      .container div {
        @include center-column;
        padding-bottom: 60px;
        @include r(767) {
          padding-bottom: 40px; } }
      h2 {
        margin-bottom: 0 !important;
        position: relative;
        display: inline-flex;
        align-items: flex-end;
        width: auto;
        max-width: 100%;
        transform: none;
        height: 70px;
        left: 0;
        @include r(767) {
          height: 30px; }
        &:after {
          content: '';
          width: 95%;
          left: 5px;
          height: 10px;
          background-color: #2cfffe;
          display: block;
          position: absolute;
          bottom: 16px;
          z-index: -1;
          @include r(767) {
            height: 5px;
            bottom: 6px; } } }

      span {
        display: inline-flex;
        align-items: flex-end;
        position: relative;
        height: 50px;
        strong {
          font-size: 50px;
          font-weight: bold;
          line-height: 1.1;
          letter-spacing: -1px;
          text-align: center;
          color: #131c2e;
          display: block;
          @include r(767) {
            font-size: 21px; } }
        @include r(767) {
          height: 22px; }
        &:after {
          content: '';
          width: 100%;
          height: 10px;
          background-color: #2cfffe;
          display: block;
          position: absolute;
          bottom: 14px;
          left: 0;
          z-index: -1;
          @include r(767) {
            bottom: 7px;
            height: 4px; } } } }
    h2 {
      max-width: 990px;
      margin-bottom: 110px;
      @include r(767) {
        margin-bottom: 40px;
        font-size: 36px; } }
    &__img {
      @include center-y;
      height: 100%;
      @include r(767) {
        padding-top: 40px; }
      img {
        width: 100%;
        max-width: 440px; }
      &--conver {
        img {
          max-width: 100%; } } }
    .features-scroller__row {
      padding-bottom: 170px;
      @include r(767) {
        padding-bottom: 100px; } }
    .features-scroller__text {
      p {
        max-width: 520px;
        width: 100%; }
      strong {
        font-size: 25px;
        font-weight: bold;
        line-height: 1.4;
        color: #131c2e;
        margin-bottom: 15px;
        display: block;
        @include r(767) {
          font-size: 18px;
          margin-bottom: 10px;
          text-align: center; } }
      a {
        display: block;
        font-size: 18px;
        font-weight: 600;
        color: #131c2e;
        margin-bottom: 55px;
        @include r(767) {
          margin-bottom: 30px;
          text-align: center; }
        &:hover {
          text-decoration: underline;
          i {
            transform: translate(8px); } }
        i {
          margin-left: 10px;
          font-size: 12px;
          color: #006dff;
          @include tr; } }
      b {
        font-size: 14px;
        font-weight: 600;
        line-height: 1.43;
        color: #616161;
        display: block;
        @include r(767) {
          text-align: center; }
 }        // max-width: 280px
      span {
        font-size: 14px;
        font-weight: normal;
        line-height: 1.43;
        color: #616161;
        margin-bottom: 20px;
        display: block;
        max-width: 530px;
        width: 100%;
        position: relative;
        @include r(767) {
          text-align: center; }
        &:before {
          content: '';
          background-image: url('../img/vector-smart-object.png');
          @include cover;
          width: 85px;
          height: 60px;
          display: block;
          position: absolute;
          top: -20px;
          left: -20px;
          z-index: -1; } } } }

  &__platform {
    padding: 100px 0 140px;
    @include r(767) {
      padding: 50px 0 0; }
    &__title {
      h2 {
        max-width: 100%;
        margin-bottom: 35px; }
      p {
        font-size: 25px;
        line-height: 1.4;
        text-align: center;
        color: #171717;
        max-width: 810px;
        @include relative-x;
        display: block;
        padding-bottom: 140px;
        @include r(767) {
          padding-bottom: 90px;
          font-size: 18px; } } }
    .message__channels-other__block {
      position: relative;
      background-color: #2cfffe;
      @include r(767) {
        margin-bottom: 100px; }
      img {
        width: 110px;
        position: absolute;
        top: -50px;
        left: 10px; }
      a {
        font-size: 18px;
        font-weight: 600;
        i {
          font-size: 12px;
          color: #006dff; } } } }
  &__presentation {
    padding: 140px 0;
    background-color: #0f0f20;
    background-image: url('../img/dxcard/dx-card-bg.png');
    @include cover;
    @include r(767) {
      padding: 50px 0; }
    &__img {
      height: 100%;
      @include center-y;
      @include r(991) {
        justify-content: center;
        padding: 30px 0; }
      img {
        width: 82%;
        @include r(991) {
          width: auto;
          height: 400px; } } }
    h2, li, strong, p {
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    &__text {
      padding-right: 70px;
      @include r(1200) {
        padding-right: 20px; }
      @include r(991) {
        padding-right: 0; }

      strong {
        font-family: 'Dancing Script', cursive;
        font-size: 30px;
        font-weight: normal;
        line-height: 1;
        color: #f0d5a5;
        background-image: linear-gradient(to right, #f3d9a9, #cca56e, #cca56e);
        padding-bottom: 5px;
        display: block;
        @include r(991) {
          text-align: center; }
        @include r(767) {
          font-size: 20px; } }
      h2 {
        text-align: left;
        margin: 20px 0;
        padding: 5px 0;
        background-image: linear-gradient(to right bottom, #c8baa5, #c8baa5, #fcefd6, #fcefd6, #a19281, #a19281);
        @include r(1200) {
          font-size: 40px;
          letter-spacing: normal; }
        @include r(991) {
          text-align: center; } }
      p {
        font-size: 25px;
        font-weight: normal;
        line-height: 1.2;
        color: #f0d5a5;
        margin-bottom: 50px;
        background-image: linear-gradient(to right bottom, #f3d9a9, #f3d9a9, #f3d9a9, #cca56e, #cca56e, #cca56e);
        @include r(991) {
          text-align: center; }
        @include r(767) {
          font-size: 18px; } }
      a {
        font-size: 18px;
        font-weight: bold;
        color: #e99d39;
        width: 195px;
        height: 50px;
        padding-top: 1px;
        border-radius: 25px;
        background-color: rgb(33, 31, 36);
        @include center;
        @include r(991) {
          @include relative-x; }
        &:hover {
          i {
            transform: translate(8px); } }
        i {
          color: #f2015b;
          margin-left: 10px;
          @include tr;
          font-size: 12px; } } }
    ul {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      @include r(991) {
        align-items: center; } }
    li {
      font-size: 18px;
      font-weight: normal;
      line-height: 1.11;
      text-align: left;
      display: inline-block;
      padding: 20px 0 15px;
      padding-left: 50px;
      position: relative;
      background-image: linear-gradient(to right bottom, #f3d9a9, #f3d9a9, #cca56e, #cca56e);
      @include r(991) {
        max-width: 400px;
        width: 100%; }
      &:before {
        width: calc(100% - 50px);
        content: '';
        background-image: linear-gradient(to right, #4d484e, #16141a);
        height: 1px;
        display: block;
        position: absolute;
        bottom: 0;
        right: 0; }
      img {
        width: 26px;
        margin-right: 25px;
        position: absolute;
        left: 0;
        top: 20px; } } }


  &__businesses {
    padding-top: 150px;
    @include r(767) {
      padding-top: 50px; }
    &__block {
      border-radius: 15px;
      border: solid 1px #dfdfdf;
      background-color: #ffffff;
      padding: 20px;
      height: 100%;
      @include r(1200) {
        margin-bottom: 20px;
        height: calc(100% - 20px); }
      &-secondary {
        border: none;
        @include center-column;
        align-items: flex-start;
        b {
          font-size: 18px;
          font-weight: 600;
          color: #131c2e;
          display: block;
          margin-bottom: 25px;
          @include r(767) {
            text-align: center; } }
        a {
          font-size: 14px;
          font-weight: bold;
          line-height: 1.79;
          color: #171717;
          @include r(767) {
            text-align: center;
            display: block;
            width: 100%; }
          &:hover {
            text-decoration: underline;
            i {
              transform: translate(5px); } }
          i {
            font-size: 10px;
            margin-left: 10px;
            @include tr; } } }
      &__img {
        border-radius: 15px;
        @include center;
        align-items: flex-end;
        height: 195px;
        margin-bottom: 30px;
        &--1 {
          background-color: #2cfffe;
          img {
            width: 117px; } }
        &--2 {
          background-color: #b783f9;
          img {
            width: 95px; } }
        &--3 {
          background-color: #0ef7b5;
          align-items: center;
          img {
            width: 137px; } } }
      &__text {
        padding: 6px;
        h4 {
          font-size: 25px;
          font-weight: bold;
          line-height: 1.2;
          color: #131c2e;
          margin-bottom: 30px;
          @include r(767) {
            text-align: center; } }
        p {
          font-size: 18px;
          font-weight: normal;
          line-height: 1.39;
          color: #131c2e;
          @include r(767) {
            text-align: center; } } } }
    &__title {
      padding: 200px 0 95px;
      @include r(767) {
        padding: 100px 0 50px; }
      h2 {
        font-size: 50px;
        margin-bottom: 35px;
        max-width: 100%;
        line-height: 1.1;
        letter-spacing: -1px;
        @include r(767) {
          font-size: 24px; } }
      p {
        font-size: 25px;
        font-weight: normal;
        line-height: 1.4;
        text-align: center;
        color: #9e9e9e;
        max-width: 705px;
        width: 100%;
        @include relative-x;
        @include r(767) {
          font-size: 18px; } } }
    &__current-ideas {
      padding-bottom: 200px;
      @include r(767) {
        padding-bottom: 50px; }
      &__block {
        @include cover;
        background-size: contain;
        @include center-column;
        padding: 60px 0px;
        height: 100%;
        min-height: 385px;
        @include tr;
        &:hover {
          transform: scale(1.06); }
        &--1 {
          background-image: url('../img/home/shape-1.png'); }
        &--2 {
          background-image: url('../img/home/shape-2.png'); }
        &--3 {
          background-image: url('../img/home/shape-3.png'); }
        @include r(767) {
          margin-bottom: 40px;
          height: auto; }
        h4 {
          font-size: 25px;
          font-weight: bold;
          text-align: center;
          color: #ffffff;
          max-width: 240px;
          width: 100%;
          display: block;
          margin-bottom: 25px; }
        a {
          font-size: 18px;
          font-weight: bold;
          line-height: 1.39;
          color: #ffffff;
          &:hover {
            text-decoration: underline;
            i {
              transform: translate(8px); } }
          i {
            margin-left: 10px;
            font-size: 12px;
            color: #006dff;
            @include tr; } } } } }


  &__firms {
    padding-bottom: 190px;
    @include r(767) {
      padding-bottom: 50px; }
    h2 {
      max-width: 100%;
      margin-bottom: 75px;
      @include r(767) {
        margin-bottom: 40px; } }
    &__row {
      display: flex;
      flex-wrap: wrap;
      justify-content: center; }

    &__block {
      margin: 0 15px;
      margin-bottom: 20px;
      @include center;
      img {
        max-width: 100%; }
      &:nth-child(1) {
        img {
          height: 22px; } }
      &:nth-child(2) {
        img {
          height: 34px; } }
      &:nth-child(3) {
        img {
          height: 23px; } }
      &:nth-child(4) {
        img {
          height: 46px; } }
      &:nth-child(5) {
        img {
          height: 16px; } } } } }




@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .home__presentation h2, .home__presentation li {
    color: #cca56e;
    background: none; } }
