.pricing {
  background-color: rgb(250, 250, 250);
  &__title {
    background-color: rgb(232, 230, 239);
    padding: 180px 0;
    min-height: 600px;
    @include r(767) {
      padding: 140px 0 50px; }
    h1 {
      font-size: 70px;
      font-weight: bold;
      line-height: 1;
      letter-spacing: -1.4px;
      text-align: center;
      color: #131c2e;
      margin-bottom: 30px;
      @include r(767) {
        font-size: 50px;
        letter-spacing: normal; } } }
  &__select {
    strong {
      font-size: 14px;
      font-weight: bold;
      color: #131c2e;
      margin-bottom: 10px;
      display: block;
      text-align: center; }
    ul {
      display: flex;
      justify-content: center;
      padding-left: 5px; }
    li {
      border-radius: 17px;
      padding: 8px 15px 6px;
      margin-right: 5px;
      font-size: 14px;
      font-weight: normal;
      color: #131c2e;
      border: 1px solid transparent;
      @include tr;
      &:hover {
        border: 1px solid #fff; }
      &.active {
        background-color: #ffffff;
        font-weight: 600; } } }
  &__tariff {
    margin-top: -170px;
    @include r(767) {
      margin-top: -220px; }
    &__block {
      border-radius: 15px;
      padding: 60px 35px;
      height: 100%;
      background: #fff;
      position: relative;
      @include r(1200) {
        padding: 60px 35px;
        margin-bottom: 30px;
        height: auto; }
      @include r(767) {
        padding: 50px 20px 90px; }
      &-secondary {
        border-top-left-radius: 0;
        border-top-right-radius: 0; }
      &-medal {
        position: absolute;
        left: 0;
        top: 0;
        width: 58px;
        height: 58px;
        padding: 8px;
        overflow: hidden;
        border-top-left-radius: 15px;
        &:before {
          content: '';
          position: absolute;
          left: -38px;
          width: 58px;
          height: 58px;
          display: block;
          top: -38px;
          border: 38px solid transparent;
          border-top: 38px solid #000;
          transform: rotate(135deg);
          z-index: 1; }
        img {
          width: 17px;
          position: relative;
          z-index: 2; } }
      &__mobile {
        @include r(767) {
          display: none; }
        &-btn {
          width: 100%;
          @include center-column;
          font-size: 14px;
          font-weight: 600;
          line-height: 1.43;
          text-align: center;
          color: #000;
          padding-bottom: 20px;
          padding-top: 20px;
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          &.active {
            i {
              transform: rotate(180deg); } } } }
      &__popular {
        position: absolute;
        top: -30px;
        left: 0;
        width: 100%;
        background-color: #0ef7b5;
        padding: 7px;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
        @include r(991) {
          top: 0; }
        p {
          font-size: 14px;
          font-weight: bold;
          text-align: center;
          color: #131c2e;
          text-transform: uppercase; } }
      &__title {
        h2 {
          font-size: 50px;
          font-weight: bold;
          line-height: 1.1;
          letter-spacing: -1px;
          text-align: center;
          color: #131c2e;
          margin-bottom: 20px;
          @include r(767) {
            letter-spacing: normal;
            font-size: 40px; } }
        p {
          font-size: 14px;
          color: #131c2e;
          text-align: center; }
        div {
          text-align: center;
          padding: 40px 0 45px;
          position: relative;
          @include r(767) {
            padding: 20px 0; }
          span {
            font-size: 30px;
            font-weight: bold;
            color: #131c2e; }
          b {
            display: block;
            position: absolute;
            text-align: center;
            width: 100%;
            font-size: 14px;
            font-weight: normal;
            line-height: 1.79;
            color: #131c2f; }
          b {
            top: 10px;
            @include r(767) {
              top: 0; } }
          em {
            font-size: 14px;
            font-weight: bold;
            font-style: normal;
            color: #131c2e; }
          strong {
            font-size: 14px;
            font-weight: normal;
            color: #131c2e;
            position: relative;
            top: -10px;
            right: 2px; }
          i {
            font-size: 14px;
            font-weight: normal;
            color: #131c2e;
            position: relative;
            top: -12px;
            right: 0px; } } }
      &__btn {
        a {
          font-size: 18px;
          font-weight: bold;
          line-height: 1.72;
          color: #ffffff;
          width: 100%;
          height: 50px;
          border-radius: 25px;
          background-color: #131c2e;
          border: 1px solid #131c2e;
          @include center;
          @include relative-x;
          &:hover {
            background: none;
            color: #131c2e; } } }
      &__includes {
        padding: 60px 0 20px;
        min-height: 125px;
        h3 {
          font-size: 14px;
          font-weight: 600;
          text-align: center;
          color: #131c2e; }
        p {
          font-size: 12px;
          text-align: center;
          color: #131c2e;
          margin-top: 20px; } }
      &__all {
        margin-top: 40px;
        h3 {
          margin-bottom: 30px;
          height: 30px;
          a {
            font-size: 20px;
            font-weight: bold;
            line-height: 1;
            text-align: center;
            color: #131c2e; } }
        li {
          font-size: 16px;
          line-height: 1.41;
          color: #131c2e;
          display: flex;
          margin-bottom: 30px;
          span {
            font-size: 14px;
            line-height: 1.43;
            color: #737373; }
          i {
            margin-right: 10px;
            color: #0ef7b5;
            font-size: 16px;
            margin-top: 2px; } } } }
    &__info {
      margin-top: 20px;
      p {
        font-size: 12px;
        text-align: right;
        color: #131c2e;
        margin-bottom: 80px;
        @include r(767) {
          margin-bottom: 30px; } }
      span {
        display: block;
        font-size: 18px;
        font-weight: normal;
        line-height: 1.39;
        text-align: center;
        color: #131c2e; }
      a {
        color: #131c2e;
        font-weight: 600;
        &:hover {
          text-decoration: underline; } } } }
  &__questions {
    padding: 156px 0 170px;
    @include r(767) {
      padding: 70px 0 50px; }
    h2 {
      font-size: 50px;
      font-weight: bold;
      letter-spacing: -1px;
      color: #131c2e;
      text-align: center;
      margin-bottom: 85px;
      @include r(767) {
        margin-bottom: 40px;
        font-size: 33px; } }
    &__block {
      border-radius: 10px;
      box-shadow: 0px 1.5px 5px 0 rgba(0, 0, 0, 0.11);
      border: solid 0 #000000;
      background-color: $white;
      padding: 35px;
      margin-bottom: 20px;
      display: inline-block;
      width: 100%;
      @include r(767) {
        padding: 25px 20px; }
      &__text {
        margin-top: 20px; }
      strong {
        font-size: 18px;
        font-weight: bold;
        line-height: 1.67;
        letter-spacing: 0.09px;
        color: #131a2e;
        position: relative;
        display: block;
        padding-right: 25px;
        cursor: pointer;
        @include tr;
        &:hover {
          color: #000; }
        &.active {
          i {
            transform: rotate(0deg); } }
        i {
          position: absolute;
          right: 0;
          top: 5px;
          transform: rotate(180deg);
          @include tr;
          font-size: 15px; } }
      div, li, b, a {
        font-size: 18px;
        line-height: 1.67;
        color: #131c2f;
        margin-bottom: 40px;
        &:last-child {
          margin-bottom: 0; } }
      a {
        &:hover {
          text-decoration: underline; } }
      ul {
        margin-bottom: 40px; }
      li {
        margin-bottom: 0;
        line-height: 1.67;
        padding-left: 20px;
        position: relative;
        &:before {
          content: '';
          width: 9px;
          height: 9px;
          position: absolute;
          left: 0;
          top: 10px;
          background: #131c2f;
          border-radius: 50%;
          margin-right: 10px;
          display: inline-block; } }
      b {
        margin-bottom: 0;
        font-weight: 600; } } } }
