.customer-success {
  background: rgb(244, 244, 244);
  padding: 120px 0;
  background-image: url('../img/testimonial-bg.png');
  background-size: auto 100%;
  background-repeat: no-repeat;
  @include rmin(1200) {
    background-position: 0 0 !important; }
  @include r(1999) {
    background-position: 40% 0; }
  @include r(767) {
    padding: 60px 0; }
