  &__text {
    h3 {
      font-size: 80px;
      font-weight: bold;
      text-align: left;
      letter-spacing: -4px;
      color: #131c2e;
      margin-bottom: 30px;
      @include r(767) {
        max-width: 100%;
        margin-top: 50px;
        text-align: center;
        font-size: 30px;
        margin-bottom: 30px;
        letter-spacing: normal; } }
